import React from 'react';
import styled from 'styled-components';
import {
  DOT,
  BUCKET,
  PICKER,
  ERASER,
  MOVE,
  SAMECOLOR,
} from '../../modules/paintTool';
import CustomButton from '../common/CustomButton';
import { TiPencil, TiPipette } from 'react-icons/ti';
import { BsFillBucketFill } from 'react-icons/bs';
import { FaEraser, FaHandPaper, FaBorderAll } from 'react-icons/fa';
import ToolTip from '../common/ToolTip';

const PaintToolBlock = styled.div`
  /* display: flex; */
  /* height: 64px; */
`;

const PaintButtonBlock = styled.div`
  display: flex;
  height: 32px;
`;

const ToolShortcut = styled.span`
  color: orange;
  font-size: 12px;
  font-weight: bold;
  margin-left: 3px;
`;

const ToolTipSpan = styled.div`
  color: #999;
  font-size: 10px;
`;

const ToolTipKey = styled.span`
  padding: 2px;
  border: 1px Solid #999;
  font-size: 0.8em;
  margin-right: 5px;
  text-align: center;
  border-radius: 3px;
  display: inline-block;
  line-height: 10px;
`;

const PaintTool = ({ paintTools, onChangePaintTool, selectedPaintTool }) => {
  return (
    <PaintToolBlock>
      <PaintButtonBlock>
        <ToolTip
          placement="top"
          tooltip={
            <div>
              Pan Tool<ToolShortcut>{`(${paintTools[DOT].key})`}</ToolShortcut>
            </div>
          }
        >
          <CustomButton
            selected={selectedPaintTool === DOT}
            onClick={() => onChangePaintTool(DOT)}
          >
            <TiPencil />
          </CustomButton>
        </ToolTip>

        <ToolTip
          placement="top"
          tooltip={
            <div>
              Bucket Tool
              <ToolShortcut>{`(${paintTools[BUCKET].key})`}</ToolShortcut>
            </div>
          }
        >
          <CustomButton
            selected={selectedPaintTool === BUCKET}
            onClick={() => onChangePaintTool(BUCKET)}
          >
            <BsFillBucketFill />
          </CustomButton>
        </ToolTip>
        <ToolTip
          placement="top"
          tooltip={
            <div>
              Colorpicker Tool
              <ToolShortcut>{`(${paintTools[PICKER].key})`}</ToolShortcut>
            </div>
          }
        >
          <CustomButton
            selected={selectedPaintTool === PICKER}
            onClick={() => onChangePaintTool(PICKER)}
          >
            <TiPipette />
          </CustomButton>
        </ToolTip>
      </PaintButtonBlock>
      <PaintButtonBlock>
        <ToolTip
          placement="top"
          tooltip={
            <div>
              Eraser Tool
              <ToolShortcut>{`(${paintTools[ERASER].key})`}</ToolShortcut>
            </div>
          }
        >
          <CustomButton
            selected={selectedPaintTool === ERASER}
            onClick={() => onChangePaintTool(ERASER)}
          >
            <FaEraser />
          </CustomButton>
        </ToolTip>
        <ToolTip
          placement="top"
          tooltip={
            <div>
              Move Tool
              <ToolShortcut>{`(${paintTools[MOVE].key})`}</ToolShortcut>
              <ToolTipSpan>
                <ToolTipKey>ALT</ToolTipKey>Wrap mod
              </ToolTipSpan>
            </div>
          }
        >
          <CustomButton
            selected={selectedPaintTool === MOVE}
            onClick={() => onChangePaintTool(MOVE)}
          >
            <FaHandPaper />
          </CustomButton>
        </ToolTip>
        <ToolTip
          placement="top"
          tooltip={
            <div>
              Bucket all same color pixel
              <ToolShortcut>{`(${paintTools[SAMECOLOR].key})`}</ToolShortcut>
            </div>
          }
        >
          <CustomButton
            selected={selectedPaintTool === SAMECOLOR}
            onClick={() => onChangePaintTool(SAMECOLOR)}
          >
            <FaBorderAll />
          </CustomButton>
        </ToolTip>
      </PaintButtonBlock>
    </PaintToolBlock>
  );
};

export default React.memo(PaintTool);
