import React, { useCallback } from 'react';
import styled from 'styled-components';
import GetAppIcon from '@material-ui/icons/GetApp';
import PublishIcon from '@material-ui/icons/Publish';
import CustomButton from '../common/CustomButton';
import FiberNewIcon from '@material-ui/icons/FiberNew';
import SaveIcon from '@material-ui/icons/Save';
import ToolTip from '../common/ToolTip';

const ButtonBlock = styled.div`
  display: flex;
  height: 40px;
`;

const SaveLoad = ({ newProjectHandle, saveHandle, dialogOpenHandle }) => {
  const onClickNewProject = useCallback(() => {
    newProjectHandle();
  }, [newProjectHandle]);

  return (
    <React.Fragment>
      <ButtonBlock>
        <ToolTip placement="top" tooltip={<>Open new project</>}>
          <CustomButton onClick={onClickNewProject}>
            <FiberNewIcon />
          </CustomButton>
        </ToolTip>
        <ToolTip placement="top" tooltip={<>Save to localStorage</>}>
          <CustomButton onClick={saveHandle}>
            <PublishIcon />
          </CustomButton>
        </ToolTip>
        <ToolTip placement="top" tooltip={<>Load from localStorage</>}>
          <CustomButton onClick={() => dialogOpenHandle('Load')}>
            <GetAppIcon />
          </CustomButton>
        </ToolTip>
      </ButtonBlock>
      <ButtonBlock>
        <ToolTip
          placement="top"
          tooltip={<>Download dotArt in diffrent formats</>}
        >
          <CustomButton onClick={() => dialogOpenHandle('DownLoad')}>
            <SaveIcon />
          </CustomButton>
        </ToolTip>
        <ToolTip placement="top" tooltip={<>Open new project</>}>
          <CustomButton onClick={() => dialogOpenHandle('Css')}>
            css
          </CustomButton>
        </ToolTip>
      </ButtonBlock>
    </React.Fragment>
  );
};

export default React.memo(SaveLoad);
